/*page home*/
.home{
  background-image: url('../img/backgrounds/home.jpg');
  background-size: 100% 100%;
  position: relative;
}
.content.home{
  padding-top: 0;
}

/*cube*/

#wrapper{
  -webkit-perspective:1100px;
  perspective:1100px;
  margin: 0 auto;
  width: 184px;
}

#cube{
  position:relative;
  width: 184px;
  height: 435px;
  -webkit-transform-origin:50% 50% -92px;
  transform-origin:50% 50% -92px;
  -webkit-transform-style:preserve-3d;
  transform-style:preserve-3d;
}
body.is_safari #cube{
  -webkit-transform-origin:50% -92px;
  transform-origin:50% -92px;
}
.play.active{
  background:none;
  width: 100px;
  height: 100px;
  margin-top: -70px;
  margin-left: 11px;
}

.side {
  width: 184px;
  height: 435px;
  background: rgba(0, 0, 0, 0.2);
  background-repeat:no-repeat;
  background-position:center;
  position: absolute;
  -webkit-transform-origin: 50% 50% -92x;
  transform-origin: 50% 50% -92px;
  -webkit-transition: 0.5s;
  transition:0.5s;
  -webkit-backface-visibility:hidden;
  backface-visibility:hidden;
}
#side1{
  -webkit-transform:rotateY(0deg);
  transform:rotateY(0deg);
  background-image: url('../img/backgrounds/side1.jpg');
}
#side2{
  -webkit-transform:rotateY(90deg);
  transform:rotateY(90deg);
  background-image: url('../img/backgrounds/side2.jpg');
}
#side3{
  -webkit-transform:rotateY(-90deg);
  transform:rotateY(-90deg);
  background-image: url('../img/backgrounds/side3.jpg');
}
#side4{
  -webkit-transform:rotateX(90deg);
  transform:rotateX(90deg);
}
#side6{
  -webkit-transform:rotateY(180deg);
  transform:rotateY(180deg);
  background-image: url('../img/backgrounds/side4.jpg');
}
/*end cube*/
